import { NextRequest, NextResponse } from "next/server";
import supabase from "@/utils/supabase/client";

// GET all companies
export async function GET() {
  try {
    const { data, error } = await supabase
      .from("insurance_companies")
      .select(`
        *,
        category:insurance_categories(id, name),
        subcategory:insurance_subcategories(id, name)
      `)
      .eq("is_active", true)
      .order("created_at", { ascending: false });

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ companies: data });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// POST create a new company
export async function POST(req: NextRequest) {
  try {
    const { name, category_id, subcategory_id, description, website_url, logo_url } = await req.json();

    if (!name || !category_id || !logo_url)
      return NextResponse.json({ error: "Name, category, and logo URL are required" }, { status: 400 });

    const { data, error } = await supabase
      .from("insurance_companies")
      .insert([{ name, category_id, subcategory_id, description, website_url, logo_url, is_active: true }])
      .select();

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ company: data[0] });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// PUT update a company
export async function PUT(req: NextRequest) {
  try {
    const { id, name, category_id, subcategory_id, description, website_url, logo_url } = await req.json();

    if (!id) return NextResponse.json({ error: "Missing company ID" }, { status: 400 });

    const { error } = await supabase
      .from("insurance_companies")
      .update({ name, category_id, subcategory_id, description, website_url, logo_url })
      .eq("id", id);

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ success: true });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// DELETE (soft delete) a company
export async function DELETE(req: NextRequest, { params }: { params: { id: string } }) {
  try {
    const { id } = params;
    if (!id) return NextResponse.json({ error: "Missing company ID" }, { status: 400 });

    const { error } = await supabase
      .from("insurance_companies")
      .update({ is_active: false })
      .eq("id", id);

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ success: true });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}
